import React, { Component } from "react";
import Header from "parts/Header";
import Detail from "parts/Detail";
import Footer from "parts/Footer";
// import { DetailData } from "utils/details_data.js";
import { connect } from "react-redux";
import { fetchPage, submitComment } from "store/actions/page";
import { useParams } from "react-router-dom";
import autoBind from "auto-bind";

/* eslint-disable */
class HomeDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // data: DetailData(),
      data_comment: {
        date: "",
        user: "",
        comment: "",
        tutorialsId: "",
      },
      burger_show_hide: false,
    };
    autoBind(this);
  }
  // React Lifecycle
  componentDidMount() {
    window.scrollTo(0, 0);
    window.addEventListener("scroll", this.listenToScroll);
    if (!this.props.page[this.props.params.id])
      this.props.fetchPage(
        `${process.env.REACT_APP_HOST}/api/v1/member/describe-page/${this.props.params.id}`,
        this.props.params.id
      );
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.listenToScroll);
  }
  // Event Handler
  listenToScroll = () => {
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop;
    const height =
      document.documentElement.scrollHeight -
      document.documentElement.clientHeight;
    const scrolled = winScroll / height;
    this.setState({
      theposition: scrolled,
    });
  };
  // onClick
  onClickBurgerStateHandler() {
    this.setState({
      burger_show_hide: !this.state.burger_show_hide,
    });
  }
  // Submit Comment
  addComment(your_name, add_comment) {
    const {
      props: { page, params },
      state: { data_comment },
    } = this;

    data_comment.date = new Date().toJSON().slice(0, 10).toLocaleString();
    data_comment.user = your_name;
    data_comment.comment = add_comment;
    data_comment.tutorialsId = page[params.id]._id;

    const payload = new FormData();
    payload.append("date", data_comment.date);
    payload.append("user", data_comment.user);
    payload.append("comment", data_comment.comment);
    payload.append("tutorialsId", data_comment.tutorialsId);
    submitComment(payload)


    // Checking Form data value
    // for (let [name, value] of payload) {
    //   alert(`${name} = ${value}`); // kunci1=nilai1, maka kunci2=nilai2
    // }
  }

  render() {
    const {
      props: { page, params },
      state: { burger_show_hide, theposition },
      onClickBurgerStateHandler,
      addComment,
    } = this;

    if (!page[params.id]) return null;

    return (
      <>
        <Header
          onClickBurgerStateHandler={onClickBurgerStateHandler}
          burger_show_hide={burger_show_hide}
          style={
            !theposition > 0.05
              ? { backgroundColor: "#cdcdcd", boxShadow: "none" }
              : null
          }
        />
        <main>
          <div className="container home-detail">
            <Detail data={page[params.id]} addComment={addComment} />
          </div>
        </main>
        <Footer />
      </>
    );
  }
}

const mapStateProps = (state) => ({
  page: state.page,
});
const withRouter = (WrappedComponent) => (props) => {
  const params = useParams();
  // etc... other react-router-dom v6 hooks
  return (
    <WrappedComponent
      {...props}
      params={params}
      // etc...
    />
  );
};
export default connect(mapStateProps, { fetchPage })(withRouter(HomeDetail));
