import React, { useState } from "react";
// import Button from "elements/Button";

/* eslint-disable */
export default function Comment({ comment, addComment }) {
  const [inputYourName, setYourName] = useState("");
  const [inputAddComment, setAddComment] = useState("");

  const onChangeYourNameHandler = (e) => {
    setYourName(e.target.value);
  };
  const onChangeAddCommentHandler = (e) => {
    setAddComment(e.target.value);
  };
  const onSubmitHandler = (e) => {
    // e.preventDefault();
    addComment(inputYourName,inputAddComment)
  };
  const onClickResetHandler = () => {
    setYourName("");
    setAddComment("");
  };

  return (
    <div className="detail-comment">
      <div className="input-comment">
        <div className="head-comment">
          <h2>Comment</h2>
          <p>Other Comment</p>
        </div>
        <div className="input-comment-form">
          <form onSubmit={onSubmitHandler}>
            <input
              type="text"
              className="text"
              name=""
              id=""
              placeholder="Your Name"
              value={inputYourName}
              onChange={onChangeYourNameHandler}
            />
            <input
              type="text"
              className="text"
              name=""
              id=""
              placeholder="Add Comment"
              value={inputAddComment}
              onChange={onChangeAddCommentHandler}
            />
            {/* <Button type="link" href="">
              reset
            </Button>
            <Button type="link" href="">
              comment
            </Button> */}
            <button type="button" onClick={onClickResetHandler}>
              reset
            </button>
            <button type="submit">comment</button>
          </form>
        </div>
      </div>
      <div className="comment-rate">
        {/* you must change this the data */}
        {comment.map((e, i) => (
          <div key={i} className="card-comment-rate">
            <div className="head-comment">
              <h4>{e.user}</h4>
              <small>{e.date}</small>
            </div>      
            <p>{e.comment}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
