import React from "react";

import Button from "elements/Button";
import Breadcrumb from "elements/Breadcrumb";
import Comment from "./Comment";

/* eslint-disable */
export default function Detail({ data, addComment }) {
  const {
    title,
    tagline,
    description,
    breadcrumb,
    softusedId,
    otherTutorialId,
    videoId,
    reviewId,
    isDownload,
  } = data;
  return (
    <>
      <section>
        <div className="detail-home-wrapper">
          <div className="detail-home">
            <div className="detail-head-title">
              <Breadcrumb data={breadcrumb}></Breadcrumb>
            </div>
            <div className="detail-video-wrapper">
              <div className="detail-video">
                <h2>{title}</h2>
                <p>{tagline}</p>
                <iframe
                  src={`${
                    videoId.length > 0
                      ? videoId[0].videoUrl
                      : "https://www.youtube.com/xxx"
                  }`}
                  allow="autoplay; encrypted-media"
                  title="video"
                  allowFullScreen
                  frameBorder={0}
                />
              </div>
            </div>
          </div>
        </div>
      </section>
      <section>
        <div className="detail-description-wrapper">
          <div className="detail-description">
            <div className="detail-description-left">
              <h2>{title}</h2>
              <p>{description}</p>
              <div className="detail-description-download">
                {isDownload ? (
                  <>
                    <h3>Download assets</h3>
                    <Button type="link" href="">
                      Download
                    </Button>
                    <p>
                      Anda bisa mengambill beberapa asset yang saya buat untuk
                      bahan pembelajaran
                    </p>
                  </>
                ) : (
                  <></>
                )}
              </div>
            </div>
            <div className="detail-description-right">
              {softusedId.length > 0 ? (
                <>
                  <h2>Sofware use</h2>
                  {softusedId.map((e, i) => (
                    <img
                      key={i}
                      src={`${process.env.REACT_APP_HOST}/${e.imageUrl}`}
                      alt={e.title}
                    />
                  ))}
                </>
              ) : (
                <></>
              )}
            </div>
          </div>
        </div>
      </section>
      <section>
        <div className="detail-comment-wrapper">
          <Comment comment={reviewId} addComment={addComment} />
        </div>
      </section>
      <section>
        <div className="galery-videos">
          <div>
            <div className="galery-head">
              {otherTutorialId.length > 0 ? (
                <span className="tutorial-title">
                  <h2>Other videos</h2>
                  <p>tutorial after effect</p>
                </span>
              ) : (
                <></>
              )}
            </div>
            <div className="image-gallery">
              {otherTutorialId.map(({ _id, imageUrl, title, description }) => (
                <Button href="/other" type="link" key={_id}>
                  <img
                    src={`${process.env.REACT_APP_HOST}/${imageUrl}`}
                    alt=""
                  />
                  <div className="tagline">
                    <div className="title">
                      <h3>{title}</h3>
                      <p>{description}</p>
                    </div>
                    <div className="other"></div>
                  </div>
                </Button>
              ))}
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
