import React from "react";
import Brand from "parts/Brand";
import Button from "elements/Button";
/* eslint-disable */
export default function Header(props) {
  return (
    <header>
      <nav style={props.style && props.style}>
        <Brand />
        {/* Dekstop Menus */}
        <ul className="dekstop-menu">
          <li>
            <Button href="/" type="nav">
              Home
            </Button>
          </li>
          <li>
            <Button href="/pages/blog" type="nav">
              Blog
            </Button>
          </li>
          <li>
            <Button
              href="https://www.youtube.com/channel/UCDEDU4u6fDDLV1nBnzrOAeg"
              type="link"
              isExternal
            >
              Youtube
            </Button>
          </li>
          <li>
            <Button href="/pages/contact" type="nav">
              Contact
            </Button>
          </li>
        </ul>

        {/* Mobile Menus */}
        <div className="mobile-menu" onClick={props.onClickBurgerStateHandler}>
          <div className="wrap">
            <div
              className={`burger ${props.burger_show_hide ? " active" : ""}`}
            >
              <div className="strip">
                <div></div>
                <div></div>
                <div></div>
              </div>
            </div>
          </div>
          {props.burger_show_hide && (
            <ul className="mobile-nav">
              <li>
                <Button
                  href="/"
                  type="nav"
                >
                  Home
                </Button>
              </li>
              <li>
                <Button
                  href="/pages/blog"
                  type="nav"
                >
                  Blog
                </Button>
              </li>
              <li>
                <Button
                  href="https://www.youtube.com/channel/UCDEDU4u6fDDLV1nBnzrOAeg"
                  type="link"
                  isExternal
                >
                  Youtube
                </Button>
              </li>
              <li>
                <Button
                  href="/pages/contact"
                  type="nav"
                >
                  Contact
                </Button>
              </li>
            </ul>
          )}
        </div>
      </nav>
    </header>
  );
}
