import React, { Component } from "react";
import Header from "parts/Header";
import GaleryList from "parts/GaleryList";
import Footer from "parts/Footer";
// import { GaleryDataList } from "utils/gelary_data_list.js";
import { connect } from "react-redux";
import { fetchPage } from "store/actions/page";
import { useParams } from "react-router-dom";
import autoBind from "auto-bind";

/* eslint-disable */
class HomeList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // data: GaleryDataList(),
      query_search: "",
      search_show_hide: false,
      burger_show_hide: false,
    };
    autoBind(this);
  }
  // React Lifecycle
  componentDidMount() {
    window.scrollTo(0, 0);
    if (!this.props.page[this.props.params.id])
    this.props.fetchPage(
      `${process.env.REACT_APP_HOST}/api/v1/member/tutorial-page/${this.props.params.id}`,
      this.props.params.id
    );
  }
  // onChange
  onChangeSearchHandler = (e) => {
    e.target.value.length === 0 && this.setState({ search_show_hide: false });
    this.setState({ query_search: e.target.value });
  };
  // onClick
  onClickSearchStateHandler() {
    this.setState({ search_show_hide: !this.state.search_show_hide });
  }
  onClickBurgerStateHandler() {
    this.setState({
      burger_show_hide: !this.state.burger_show_hide,
    });
  }
  render() {
    const {
      props: { page, params },
      state: { burger_show_hide, search_show_hide , query_search },
      onClickBurgerStateHandler,
      onClickSearchStateHandler,
      onChangeSearchHandler,
    } = this;

    if (!page[params.id]) return null;

    return (
      <>
        <Header
          onClickBurgerStateHandler={onClickBurgerStateHandler}
          burger_show_hide={burger_show_hide}
        />
        <main>
          <div className="container home-galery-list">
            <GaleryList
              data={page[params.id]}
              query_search={query_search}
              search_show_hide={search_show_hide}
              onClickSearchStateHandler={onClickSearchStateHandler}
              onChangeSearchHandler={onChangeSearchHandler}
            />
          </div>
        </main>
        <Footer />
      </>
    );
  }
}

const mapStateProps = (state) => ({
  page: state.page,
});
const withRouter = (WrappedComponent) => (props) => {
  const params = useParams();
  // etc... other react-router-dom v6 hooks
  return (
    <WrappedComponent
      {...props}
      params={params}
      // etc...
    />
  );
};
export default connect(mapStateProps, { fetchPage })(withRouter(HomeList));
