import React from "react";
import MyPhoto from "assets/images/my_photo.png";
import Rectangle from "assets/icon/rectangle.svg";
import RectangleObject from "assets/icon/rectangle_object.svg";
import PolygonObject from "assets/icon/polygon_object.svg";
import Ellipse0bject from "assets/icon/ellipse_object.svg";

import Button from "elements/Button";
/* eslint-disable */
export default function Hero(props) {
  const showMoreTutorial = () => {
    window.scrollTo({
      top: props.refMoreTutorial.current.offsetTop - 44.5,
      behavior: "smooth",
    });
  };

  return (
    <section>
      <div className="hero">
        <div className="hero-left">
          <div className="my-profile">
            <img src={MyPhoto} alt="?" />
            <img src={Rectangle} alt="?" />
            <img src={RectangleObject} alt="?" />
            <img src={PolygonObject} alt="?" />
            <img src={Ellipse0bject} alt="?" />
          </div>
        </div>
        <div className="hero-right">
          <h1>Hai, disini</h1>
          <h2>Friski Pras</h2>
          <small>dari Gray Mankiz</small>
          <span>
            Saya suka berbagai Hal tentang teknologi dan Visual, sebenarnya web
            ini dibuat untuk sekedar sharing pengalaman selama ini, setelah lama
            menggeluti bidang visual, semoga kalian terbantu
          </span>
          <div className="call-to-action">
            <Button onClick={showMoreTutorial} type="link" href="#">
              <p>More tutorial</p>
            </Button>
            <Button
              type="link"
              href="https://friskipradana.github.io/front-mograph-profile-web-deploy/"
              isExternal
            >
              <p>Show my work</p>
            </Button>
          </div>
        </div>
      </div>
    </section>
  );
}
