import React from "react";
import Search from "assets/icon/search.svg";
import SearchWhite from "assets/icon/search_white.svg";

import Button from "elements/Button";
import SearchInput from "elements/Search/SearchInput";
import SearchIcon from "elements/Search/SearchIcon";

/* eslint-disable */
export default function Galery({
  data,
  search_popup_show_hide,
  onClickSearchStateHandler,
  refMoreTutorial,
  onChangeSearchHandler,
  query_search,
}) {
  const {
    // image,
    name,
    tagline,
    image_features,
    name_features,
    tagline_features,
    image_features_2,
    name_features_2,
    tagline_features_2,
    search_list,
    // galery,
    categories,
  } = data;
  const Searchfilter = search_list.filter((e) => {
    return e.title.toLowerCase().indexOf(query_search.toLowerCase()) !== -1;
  });

  return (
    <section ref={refMoreTutorial}>
      <div className="galery">
        <div className="galery-videos">
          {categories.map((category, index) => (
            <div key={`galery-${index}`}>
              <div className="galery-head">
                <span className="tutorial-title">
                  {/* <img src={category.image} alt={category.name} /> */}
                  <h2>{category.name}</h2>
                </span>

                {index === 0 && (
                  <SearchIcon
                    onClickSearchStateHandler={onClickSearchStateHandler}
                    SearchIcon={search_popup_show_hide ? SearchWhite : Search}
                    style={
                      search_popup_show_hide
                        ? { backgroundColor: "#f39c12" }
                        : null
                    }
                  />
                )}
              </div>
              <div className="tutorial-search">
                {index === 0 && search_popup_show_hide && (
                  <div
                    style={{
                      // backgroundColor: "gainsboro",
                      // padding: "1em",
                      // minHeight: "200px",
                      borderRadius: "5px",
                    }}
                  >
                    <SearchInput
                      onChangeSearchHandler={onChangeSearchHandler}
                      search={query_search}
                    />
                    {/* fix this loop */}
                    {query_search.length > 0 &&
                      Searchfilter.map((v, i) => (
                        <div key={i} className="search-list">
                          <Button type="link" href={v.href}>
                            {v.title}
                          </Button>
                        </div>
                      ))}
                  </div>
                )}
              </div>
             
              <div className="image-gallery">
                {category.itemId.length > 0 ? (
                  category.itemId.map(
                    ({ _id, imageId, title, description, tutorialsId }) => (
                      <Button href={`/tutorial/${_id}`} type="link" key={_id}>
                        <div className="card">
                          <img
                            src={
                              imageId[0]
                                ? `${process.env.REACT_APP_HOST}/${imageId[0].imageUrl}`
                                : ""
                            }
                            alt=""
                          />
                          <div className="tagline">
                            <div className="title">
                              <h3>{title}</h3>
                              <p>{description}</p>
                              <small>{`${tutorialsId.length} videos`}</small>
                            </div>
                          </div>
                          <div className="other"></div>
                        </div>
                      </Button>
                    )
                  )
                ) : (
                  <>Tidak ada data</>
                )}
              </div>
              
            </div>
          ))}
        </div>
        <div className="galery-features">
          <span>
            {/* <img src={`${process.env.REACT_APP_HOST}/${image}`} alt="" /> */}
            <h2 className="features">{name}</h2>
          </span>
          <small style={{ textAlign: "center", display: "block" }}>
            {tagline}
          </small>
          <div className="features-wrap">
            <div className="features-left">
              <img
                src={`${process.env.REACT_APP_HOST}/${image_features}`}
                alt=""
              />
              <div className="features-title">
                <h4>{name_features}</h4>
                <p>{tagline_features}</p>
                <Button type="link" href="/articles">
                  Show me
                </Button>
              </div>
            </div>
            <div className="features-right">
              <img
                src={`${process.env.REACT_APP_HOST}/${image_features_2}`}
                alt=""
              />
              <div className="features-title">
                <h4>{name_features_2}</h4>
                <p>{tagline_features_2}</p>
                <Button type="link" href="/assets">
                  Show me
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
