import React, { Component } from "react";
import Header from "parts/Header";
import Hero from "parts/Hero";
import Galery from "parts/Galery";
import Footer from "parts/Footer";
// import { GaleryData } from "utils/galery_data.js";
import { connect } from "react-redux";
import { fetchPage } from "store/actions/page";
import autoBind from "auto-bind";

/* eslint-disable */
class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // data: GaleryData(),
      query_search: "",
      search_popup_show_hide: false,
      burger_show_hide: false,
    };
    autoBind(this);
    this.refMoreTutorial = React.createRef();
  }
  // React Lifecycle
  componentDidMount() {
    if (!this.props.page.Home)
      this.props.fetchPage(`${process.env.REACT_APP_HOST}/api/v1/member/landing-page`,"Home");
  }
  // onChange
  onChangeSearchHandler = (e) => {
    e.target.value.length === 0 &&
      this.setState({ search_popup_show_hide: false });
    this.setState({ query_search: e.target.value });
  };
  // onClick
  onClickSearchStateHandler() {
    this.setState({
      search_popup_show_hide: !this.state.search_popup_show_hide,
    });
  }
  onClickBurgerStateHandler() {
    this.setState({
      burger_show_hide: !this.state.burger_show_hide,
    });
  }

  render() {
    const {
      props: { page },
      state: { burger_show_hide, search_popup_show_hide, query_search },
      onClickBurgerStateHandler,
      onClickSearchStateHandler,
      onChangeSearchHandler,
      refMoreTutorial,
    } = this;

    if (!page.hasOwnProperty("Home")) return null;

    return (
      <>
        <Header
          onClickBurgerStateHandler={onClickBurgerStateHandler}
          burger_show_hide={burger_show_hide}
        />
        <main>
          <div className="container home-galery">
            <Hero refMoreTutorial={refMoreTutorial} />
            <Galery
              data={page.Home}
              query_search={query_search}
              search_popup_show_hide={search_popup_show_hide}
              onClickSearchStateHandler={onClickSearchStateHandler}
              refMoreTutorial={refMoreTutorial}
              onChangeSearchHandler={onChangeSearchHandler}
            />
          </div>
        </main>
        <Footer />
      </>
    );
  }
}

const mapStateProps = (state) => ({
  page: state.page,
});
export default connect(mapStateProps, { fetchPage })(Home);
