import React from "react";
import Button from "elements/Button";
// import AE from "assets/icon/after_effect_outline.svg";
import Search from "assets/icon/search.svg";
import SearchInput from "elements/Search/SearchInput";
import SearchIcon from "elements/Search/SearchIcon";
import SearchWhite from "assets/icon/search_white.svg";

/* eslint-disable */
export default function GaleryList({
  data,
  onChangeSearchHandler,
  query_search,
  search_show_hide,
  onClickSearchStateHandler,
}) {
  const Searchfilter = data.tutorialsId.filter((e) => {
    return e.title.toLowerCase().indexOf(query_search.toLowerCase()) !== -1;
  });
  // console.log(data)
  return (
    <section>
      <div className="galery-list">
        <div className="galery-head">
          <span className="list-title">
            {/* <img src={AE} alt="" /> */}
            <h2>{data.title}</h2>
          </span>
          <SearchIcon
            onClickSearchStateHandler={onClickSearchStateHandler}
            SearchIcon={search_show_hide ? SearchWhite : Search}
            style={search_show_hide ? { backgroundColor: "#f39c12" } : null}
          />
        </div>
        <div className="list-search">
          {search_show_hide && (
            <SearchInput
              onChangeSearchHandler={onChangeSearchHandler}
              search={query_search}
            />
          )}
        </div>

        {Searchfilter.length > 0 ? (
          <div className="image-gallery-list">
            {Searchfilter.map(({ imageUrl, title, description, _id }) => (
              <Button href={`/details/${_id}`} type="link" key={_id}>
                <div className="card">
                  <img
                    src={`${process.env.REACT_APP_HOST}/${imageUrl}`}
                    alt=""
                  />
                  <div className="tagline">
                    <div className="title">
                      <h3>{title}</h3>
                      <p>{description}</p>
                    </div>
                  </div>
                </div>
              </Button>
            ))}
          </div>
        ) : (
          <div
            style={{
              padding: "5px",
              width: "200px",
              margin: "1em auto",
              textAlign: "center",
            }}
          >
             <>Tidak ada data</>
          </div>
        )}
      </div>
    </section>
  );
}
