import React from "react";
import "assets/style/style.scss";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { ToastContainer } from "react-toastify";

import Home from "pages/Home";
import HomeDetail from "pages/HomeDetail";
import HomeList from "pages/HomeList";
import Blog from "pages/Blog";
import Contact from "pages/Contact";
import Socket from "pages/Socket";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path="/front-mograph-web-deploy" index element={<Home />} />
          <Route
            path="/front-mograph-web-deploy/tutorial/:id"
            element={<HomeList />}
          />
          <Route
            path="/front-mograph-web-deploy/details/:id"
            element={<HomeDetail />}
          />
          <Route
            path="/front-mograph-web-deploy/pages/blog"
            element={<Blog />}
          />
          <Route
            path="/front-mograph-web-deploy/pages/contact"
            element={<Contact />}
          />
          <Route
            path="/front-mograph-web-deploy/pages/socket"
            element={<Socket />}
          />
        </Routes>
      </BrowserRouter>
      <ToastContainer />
    </div>
  );
}

export default App;
